// @TODO: remove these once determined they are not being used elsewhere
$mct-drawer-close-button-size: 26px;

.ds-c-drawer__header-heading {
  font-family: var(--font-family-montserrat);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  line-height: var(--font-line-height-base);
}

.ds-c-drawer__close-button {
  align-self: unset;
}

// Help drawer button with icon
// TODO: clean up this code if a different, more flexible SVG can be used instead
.ds-c-help-drawer__toggle {
  .icon-wrapper {
    position: relative;
  }

  .ds-c-icon--info-circle-outline {
    height: 18px;
    width: 18px;
  }
  &:hover {
    .icon-wrapper {
      &:before {
        background-color: var(--drawer-toggle__background-color--hover);
        border-radius: 50%;
        clip-path: circle(8px);
        content: '';
        height: 18px;
        left: 0;
        position: absolute;
        top: 2px;
        width: 18px;
        z-index: -1;
      }
    }
    .ds-c-icon--info-circle-outline {
      color: var(--drawer-toggle__color--hover);
    }
  }
}

.ds-c-button--on-dark {
  &.ds-c-help-drawer__toggle {
    &:hover {
      .icon-wrapper {
        &:before {
          background-color: rgba(var(--drawer-toggle__background-color--hover--inverse), 0.8);
        }
      }
      .ds-c-icon--info-circle-outline {
        color: var(--drawer-toggle__color--hover--inverse);
      }
    }
  }
}
